










































import TopBar from "@/components/TopBar.vue";
import BottomContent from "@/components/BottomContent.vue";
import VideoList from "@/components/VideoList.vue";
import AxiosUtils from "@/commons/AxiosUtils";
import MovieUrls from "@/commons/NetworkUtils";
import CryptoJS from 'crypto-js';
import HlsJsPlayer from 'xgplayer-hls.js';
import {Tabs, Form, FormItem, Row, Col, TabPane} from 'element-ui';
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {
    VideoList,
    TopBar,
    BottomContent,
    [Tabs.name]: Tabs,
    [TabPane.name]: TabPane,
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Row.name]: Row,
    [Col.name]: Col,
  },
})
export default class DetailPage extends Vue {

  // const [id] = useState<string>(queryData?.id||'');
  // const [title] = useState<string>(queryData?.title||'');
  // const [episode, handleEpisode] = useState<string>('');
  // const [url, handleUrl] = useState<string>('');
  // const [group, handleGroup] = useState<any>(null);
  // const [playList, handlePlayList] = useState<any>([]);
  // const [vplayer, handleVplayer] = useState<any>(null);

  id = ''
  title = ''
  activeName = 'first'
  episode = ''
  url = ''
  playList = []
  vplayer?: any
  group = {
    name: '',
    actor: '',
    director: '',
    remark: ''
  }

  created() {
    this.id = this.$route.query?.id?.toString()||'1';
    this.title = this.$route.query?.name?.toString();
    this.fetchData();
  }

  handlePlayBtnClick(url:any, episode:any) {
    this.url = url;
    this.episode = episode;
    if (this.vplayer) {
      // that.player.destroy(false)
      // that.player = new HlsJsPlayer(this.options);
      this.vplayer.src = url;
    } else {
      let player = new HlsJsPlayer({
        id: 'vs',
        url: url,
        width: '100%',
        height: '480px',
        autoplay: true,
        controlsList: [
          "volume",
          "playbackRate"
        ],
        playbackRate: [0.5, 0.75, 1, 1.5, 2] //传入倍速可选数组
      });
      this.vplayer = player;
    }
  }

  fetchData() {
    let key  = CryptoJS.enc.Utf8.parse('38383838363636363232323238383838');
    let pms = {
      groupId: this.id
    };
    let ecparams = CryptoJS.AES.encrypt(JSON.stringify(pms), key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });

    let that = this;

    AxiosUtils.get(MovieUrls.movie_urls.movie_detail, {params:{"p":ecparams.ciphertext.toString()}}).then((data) => {
      let result = data.data.data;

      that.$nextTick(()=>{
        that.group = result.group;
        that.playList = result.videos.list;

        let player = new HlsJsPlayer({
          id: 'vs',
          url: result.videos.list[0][0].url,
          width: '100%',
          height: '480px',
          autoplay: true,
          controlsList: [
            "volume",
            "playbackRate"
          ],
          playbackRate: [0.5, 0.75, 1, 1.5, 2] //传入倍速可选数组
        });

        that.vplayer = player;
        // this.$nextTick(() => {
        //   if (this.vplayer) {
        //     this.vplayer.on('play',function(){
        //       //事件名称可以在上述查询
        //       console.log('play')
        //     })
        //     this.vplayer.on('canplay',function(){
        //       //事件名称可以在上述查询
        //       console.log('canplay')
        //     })
        //     this.vplayer.on('waiting',function(){
        //       //事件名称可以在上述查询
        //       console.log('waiting')
        //     })
        //     this.vplayer.on('playing',function(){
        //       //事件名称可以在上述查询
        //       console.log('playing')
        //     })
        //   }
        // });
        that.url = result.videos.list[0][0].url;
        that.episode = result.videos.list[0][0].name
      });
    }).catch((error:any)=>{
      console.log(error)
    });
  }
}
