

















import TopBar from "@/components/TopBar.vue";
import BottomContent from "@/components/BottomContent.vue";
import VideoList from "@/components/VideoList.vue";
import AxiosUtils from "@/commons/AxiosUtils";
import MovieUrls from "@/commons/NetworkUtils";
import CryptoJS from 'crypto-js';
import {Pagination} from 'element-ui';
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {
    VideoList,
    TopBar,
    BottomContent,
    [Pagination.name]: Pagination
  },
})
export default class Zongyi extends Vue {
  listData: Array<any> = []
  current = 1
  total = 0

  created() {
    console.log(this.$route.query)
    this.current = parseInt(this.$route.query.page.toString())||1;
    this.fetchData();
  }

  handleCurrentChange(val:number) {
    console.log(`当前页: ${val}`);
    window.location.href = "/#/zongyi?page="+val;
  }

  fetchData() {
    var key  = CryptoJS.enc.Utf8.parse('38383838363636363232323238383838');
    let pms = {
      "size": 24
      , "videoType": '综艺'
      , 'current': new String((this.current-1)*24)
      // , "current": (parseInt(that.currentPage)-1)*12
      // , "contentType": that.contentType == "全部" ? "" : that.contentType
      // , "time": that.time == "全部" ? "" : that.time
      // , "location": that.location == "全部" ? "" : that.location
    };
    let ecparams = CryptoJS.AES.encrypt(JSON.stringify(pms), key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });

    let that = this;

    AxiosUtils.get(MovieUrls.movie_urls.movie_list, {params:{"p":ecparams.ciphertext.toString()}}).then((data:any) => {
      that.$nextTick(()=>{
        that.total = data.data.data.total;
        that.listData = data.data.data.list;
      });
    }).catch((error:any)=>{
      console.log(error)
    });
  }
}
